"use client";
import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import axios from "axios";

export default function HeroSection() {
  const [message, setMessage] = useState("");

  useEffect(() => {
    axios.get("http://localhost:8000/api/hello/").then((res) => {
      setMessage(res.data.message);
    });
  }, []);

  return (
    <header className="w-full bg-gradient-to-br from-blue-600 to-purple-600 text-white py-20 shadow-md rounded-2xl mb-8 flex items-center justify-center">
      <div className="max-w-3xl mx-auto px-4 text-center">
        <h1 className="text-5xl sm:text-6xl font-extrabold mb-4 font-sans drop-shadow-lg">
          {message || "Welcome to Braselton Tech Incubator"}
        </h1>
        <p className="text-lg sm:text-xl font-medium text-blue-100 mb-8">
          Accelerating innovation for startups and entrepreneurs.
        </p>
        <Button className="bg-white text-purple-700 px-8 py-4 rounded-2xl text-lg font-bold shadow-md hover:bg-purple-100 transition">
          Get Started
        </Button>
      </div>
    </header>
  );
} 